import * as Yup from 'yup';
import PropTypes from 'prop-types';
import { useCallback, useEffect, useState } from 'react';
import { useSnackbar } from 'notistack';
import { useNavigate } from 'react-router-dom';
import { Form, FormikProvider, useFormik } from 'formik';
// material
import { Box, Card, Grid, Stack, Switch, TextField, FormControlLabel, IconButton, InputAdornment } from '@mui/material';
import { DesktopDatePicker, LoadingButton } from '@mui/lab';
import { Icon } from '@iconify/react';
import eyeFill from '@iconify/icons-eva/eye-fill';
import closeFill from '@iconify/icons-eva/close-fill';
import eyeOffFill from '@iconify/icons-eva/eye-off-fill';
import Request from '../../../utils/Request';
// routes
import { PATH_DASHBOARD } from '../../../routes/paths';
//
import Label from '../../Label';
import { UploadAvatar } from '../../upload';
import { useDispatch, useSelector } from '../../../redux/store';
import { getTransactions, getAgents } from '../../../redux/slices/user';
import { fDate, fDateTime } from '../../../utils/formatTime';
// ----------------------------------------------------------------------

export default function TransactionNewForm() {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { enqueueSnackbar } = useSnackbar();
  const { transaction, agents, lotto, infotirages } = useSelector((state) => state.user);

  const NewTransactionSchema = Yup.object().shape({
    agent_id: Yup.string().required('agent is required'),
    montant: Yup.number().required('montant is required'),
    type_transaction: Yup.string().required('type transaction is required')
  });

  const formik = useFormik({
    enableReinitialize: true,
    initialValues: {
      agent_id: null,
      montant: null,
      type_transaction: null
    },
    validationSchema: NewTransactionSchema,
    onSubmit: async (values, { setSubmitting, resetForm, setErrors }) => {
      try {
        await Request.handleAddTransaction(values);
        resetForm();
        setSubmitting(false);
        enqueueSnackbar('Créer avec succès', { variant: 'success' });
        getTransactions(fDate(new Date()), fDate(new Date()));
        navigate(PATH_DASHBOARD.transaction?.root);
      } catch (error) {
        if (error?.message) {
          enqueueSnackbar(error?.message, { variant: 'error' });
        } else {
          alert(JSON.stringify(error));
        }
        setSubmitting(false);
        setErrors(error);
      }
    }
  });

  useEffect(() => {
    dispatch(getAgents());
  }, [dispatch]);

  const { errors, values, touched, handleSubmit, isSubmitting, setFieldValue, getFieldProps } = formik;

  return (
    <FormikProvider value={formik}>
      <Form noValidate autoComplete="off" onSubmit={handleSubmit}>
        <Grid container spacing={3}>
          <Grid item xs={12} md={8}>
            <Card sx={{ p: 3 }}>
              <Stack spacing={3}>
                <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
                  <TextField
                    select
                    fullWidth
                    label="Type Transaction"
                    placeholder="Type transaction"
                    {...getFieldProps('type_transaction')}
                    SelectProps={{ native: true }}
                    error={Boolean(touched.type_transaction && errors.type_transaction)}
                    // helperText={touched.type_transaction && errors.type_transaction}
                  >
                    <option value="" />
                    {[
                      { value: 'RG', name: 'Recharge' },
                      { value: 'RB', name: 'Rembourssement' },
                      { value: 'RE', name: 'Retrait' }
                    ]?.map((optio) => (
                      <option key={optio.value} value={optio.value}>
                        {optio.name}
                      </option>
                    ))}
                  </TextField>
                  <TextField
                    select
                    fullWidth
                    label="Agent"
                    placeholder="Choisir Agent"
                    {...getFieldProps('agent_id')}
                    SelectProps={{ native: true }}
                    error={Boolean(touched.agent_id && errors.agent_id)}
                    // helperText={touched.agent_id && errors.agent_id}
                  >
                    <option value="" />
                    {agents?.map((optio) => (
                      <option key={optio.id} value={optio.id}>
                        {optio.first_name} {optio.last_name}
                      </option>
                    ))}
                  </TextField>
                </Stack>

                <Stack spacing={{ xs: 3, sm: 2 }}>
                  <TextField
                    fullWidth
                    label="Montant"
                    placeholder="Entrer montant"
                    {...getFieldProps('montant')}
                    error={Boolean(touched.montant && errors.montant)}
                    // helperText={touched.montant && errors.montant}
                  />
                </Stack>

                <Box sx={{ mt: 3, display: 'flex', justifyContent: 'flex-end' }}>
                  <LoadingButton type="submit" variant="contained" loading={isSubmitting}>
                    Sauvegarder les modifications
                  </LoadingButton>
                </Box>
              </Stack>
            </Card>
          </Grid>
        </Grid>
      </Form>
    </FormikProvider>
  );
}
